export interface AbilityEffect {
  type: 'damage' | 'slow' | 'splash' | 'money' | 'range' | 'multi' | 'pierce';
  value: number;
  duration?: number;
}

export interface NationAbility {
  id: string;
  name: string;
  description: string;
  effects: AbilityEffect[];
  cooldown?: number;
}

